import AbstractView from '../abstract-view';

export default class ModalErrorView extends AbstractView {
  constructor(status) {
    super();
    this.status = status;
  }

  get template() {
    return `
      <section class="modal">
        <h2 class="modal__title"> !</h2>
        <p class="modal__text">: ${this.status}. ,  .</p>
      </section>`;
  }

  bind() {
    //
  }
}
