import AbstractView from '../abstract-view';
import Application from '../application';

export default class FailTriesView extends AbstractView {
  constructor() {
    super();
  }

  get template() {
    return `
      <section class="result">
        <div class="result__logo"><img src="img/melody-logo.png" alt=" " width="186" height="83"></div>
        <h2 class="result__title"> !</h2>
        <p class="result__total result__total--fail">    . ,    !</p>
        <button class="result__replay" type="button">  </button>
      </section>`;
  }

  bind() {
    this.element.querySelector(`.result__replay`).addEventListener(`click`, (event) => {
      event.preventDefault();
      Application.showGame();
    });
  }
}
