import AbstractView from '../abstract-view';
import Application from '../application';

export default class WelcomeView extends AbstractView {
  constructor() {
    super();
  }

  get template() {
    return `
      <section class="welcome">
        <div class="welcome__logo"><img src="img/melody-logo.png" alt=" " width="186" height="83"></div>
        <button class="welcome__button"><span class="visually-hidden"> </span></button>
        <h2 class="welcome__rules-title"> </h2>
        <p class="welcome__text"> :</p>
        <ul class="welcome__rules-list">
          <li> 5      .</li>
          <li>  3 .</li>
        </ul>
        <p class="welcome__text">!</p>
      </section>`;
  }

  bind() {
    this.element.querySelector(`.welcome__button`).addEventListener(`click`, (event) => {
      event.preventDefault();
      Application.showGame();
    });
  }
}
